import Vue from 'vue';
export default Vue.extend({
    props: {
        title: String,
        description: String
    },
    data() {
        return {
            cls: 'ep-list-item-meta'
        };
    },
    computed: {
        showContent() {
            return !!(this.title ||
                this.$slots.title ||
                this.description ||
                this.$slots.description);
        }
    }
});



