import Vue from 'vue';
export default Vue.extend({
    data() {
        const sizeCls = {
            'avatar-lg': this.size === 'large',
            'avatar-sm': this.size === 'small'
        };
        return {
            // TODO: set scale auto if image too large
            // scale: 1,
            isImgExist: true,
            className: Object.assign({ 'avatar': true }, sizeCls, { 'avatar-image': this.src, [`avatar-${this.shape}`]: this.shape })
        };
    },
    props: {
        shape: {
            type: String,
            default: 'circle'
        },
        size: {
            type: String,
            default: 'default'
        },
        src: String
    },
    methods: {
        handleImgLoadError() {
            this.isImgExist = false;
        }
    }
});



