import Vue from 'vue';
import { Badge, Popover, Button, Tabs, TabPane } from 'element-ui';
import * as moment from 'moment';
import NoticeList from './NoticeList.vue';
Vue.use(Badge);
Vue.use(Popover);
Vue.use(Button);
Vue.use(Tabs);
Vue.use(TabPane);
export default Vue.extend({
    data() {
        const activeTab = this.tabs && this.tabs.length > 0 ? this.tabs[0].title : '';
        return {
            popoverOpen: false,
            activeTab
        };
    },
    props: {
        tabs: {
            type: Array,
            default() {
                return [];
            }
        }
    },
    computed: {
        tabOptions() {
            return this.tabs.map(tab => {
                const titleShow = tab.list && tab.list.length > 0
                    ? `${tab.title} (${tab.list.length})`
                    : tab.title;
                return Object.assign({}, tab, { titleShow });
            });
        }
    },
    components: {
        NoticeList
    },
    methods: {
        onTabChange(tab, event) {
            console.log('NoticeIcon/index, onTabChange, tab:', tab, 'event:', event);
        },
        getNoticeData(noticeList) {
            if (!noticeList || noticeList.length < 0) {
                return [];
            }
            return noticeList.map(item => {
                return Object.assign({}, item, { datetime: moment(item.datetime).fromNow() });
            });
        }
    }
});



